<template>
<!--  -->
  <div>
    <el-form ref="loginForm" :model="loginForm" :rules="rules" class="loginForm">
      <h3 class="loginTitle"></h3>
      <!---->
      <el-form-item prop="username">
        <el-input v-model="loginForm.username"
                  auto-complete="false"
                  placeholder=""
                  type="text">
        </el-input>
      </el-form-item>
      <!---->
      <el-form-item prop="password">
        <el-input v-model="loginForm.password"
                  auto-complete="false"
                  placeholder=""
                  type="password">
        </el-input>
      </el-form-item>
      <!---->
      <el-form-item>
        <el-radio v-model="type" label="systemAdmin"></el-radio>
        <el-radio style="position: relative;left: 80px" v-model="type" label="dormitoryAdmin"></el-radio>
      </el-form-item>
      <!---->
      <el-button style="width: 100%" type="primary" @click="submitLogin" :loading="loginLoad"></el-button>
    </el-form>
  </div>
</template>

<script>
import Nprogress from 'nprogress'
import 'nprogress/nprogress.css'

export default {
  name: "Login",
  data() {
    return {
      captchaUrl: '',
      loginForm: {
        username: 'admin',
        password: '123456'
      },
      type: 'systemAdmin',
      loginLoad: false,
      rules: {
        username: [{required: true, message: '', trigger: 'blur'}],
        password: [{required: true, message: '', trigger: 'blur'}],
      }
    }
  },
  methods: {
    submitLogin() {
      this.$refs.loginForm.validate((valid) => {
        if (valid) {
          Nprogress.start()
          this.loginLoad = true
          let _this = this
          if (_this.type === 'dormitoryAdmin') {
            axios.get('http://localhost:9090/dormitoryAdmin/login', {params: _this.loginForm}).then(function (resp) {
              _this.loginLoad = false
              Nprogress.done()
              if (resp.data.code === -1) {
                _this.$alert('', '', {
                  confirmButtonText: ''
                })
              }
              if (resp.data.code === -2) {
                _this.$alert('', '', {
                  confirmButtonText: ''
                })
              }
              if (resp.data.code === 0) {
                //SystemAdmin
                //
                localStorage.setItem('dormitoryAdmin', JSON.stringify(resp.data.data));
                _this.$router.replace({path: '/dormitoryAdmin'})
              }
            })
          }
          if (_this.type === 'systemAdmin') {
            axios.get('http://localhost:9090/systemAdmin/login', {params: _this.loginForm}).then(function (resp) {
              _this.loginLoad = false
              Nprogress.done()
              if (resp.data.code === -1) {
                _this.$alert('', '', {
                  confirmButtonText: ''
                })
              }
              if (resp.data.code === -2) {
                _this.$alert('', '', {
                  confirmButtonText: ''
                })
              }
              if (resp.data.code === 0) {
                //SystemAdmin
                //
                localStorage.setItem('systemAdmin', JSON.stringify(resp.data.data));
                _this.$router.replace({path: '/systemAdmin'})
              }
            })
          }

        } else {
          this.$message.error('');
          // console.log('error submit!!');
          return false;
        }
      });
    }
  }
}
</script>

<style scoped>
.loginForm {
  border-radius: 15px;
  background-clip: padding-box;
  margin: 180px auto;
  width: 350px;
  padding: 15px 35px 15px 35px;
  background: white;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
}

.loginTitle {
  margin: 0 auto 40px auto;
  text-align: center;
}
</style>
